        <b-nav-item
        :active='metGene'
        @click='MetGene()'
        >Metabolic Reaction</b-nav-item>
      </b-nav>
    </div>
    <div class='container-fluid' v-if='metSen'>
      <div class="row">
        <div class="col-md-12">
          <div class="panel panel-default" id="result-panel">
            <table class="table table-bordered table-hover" id="dataset-table">
              <thead>
                <tr>
                  <th 
                  v-for="item in titles" 
                  :key="item.index">
                    <span style="font-weight: bold">{{ item }}</span>
                  </th>
                </tr>
                <tr 
                v-for="dict_item in fbdict" 
                :key="dict_item.index"
                >
                  <td 
                  v-for="item in dict_item" 
                  :key="item.index">
                  <span id='tcont'>{{ item }}</span>
                  </td>
                </tr>
              </thead>
            </table>
          </div>
        </div>
      </div>
      <div class="container text-center" id='page_basic'>
        <span>Page <input
          v-model="curPage"
          style="width:3rem"
          > of {{ totalPage }} </span>
        <b-button
        @click='MetSensor()'
        >GO</b-button>
      </div>
    </div>
    <div class='container-fluid' v-if='metGene'>
      <div class="row">
        <div class="col-md-12">
          <div class="panel panel-default" id="result-panel">
            <table class="table table-bordered table-hover" id="dataset-table">
              <thead>
                <tr>
                  <th 
                  v-for="item in titles" 
                  :key="item.index">
                    <span style="font-weight: bold">{{ item }}</span>
                  </th>
                </tr>
                <tr 
                v-for="dict_item in fbdict" 
                :key="dict_item.index"
                >
                  <td 
                  v-for="item in dict_item" 
                  :key="item.index"
                  >
                  <span id='tcont'>{{ item }}</span>
                  </td>
                </tr>
              </thead>
            </table>
          </div>
        </div>
      </div>
      <div class="text-center container" id='page_basic'>
        <span>Page <input
          v-model="curPage"
          style="width:3rem"
          > of {{ totalPage }} </span>
        <b-button
        @click='MetGene()'
        >GO</b-button>
      </div>
    </div>
    <div class='container-fluid' v-if='metInfo'>
      <div class="row">
        <div class="col-md-12">
          <div class="panel panel-default" id="result-panel">
            <table class="table table-bordered table-hover" id="dataset-table">
              <thead>
                <tr>
                  <th 
                  v-for="item in titles" 
                  :key="item.index">
                    <span style="font-weight: bold">{{ item }}</span>
                  </th>
                </tr>
                <tr 
                v-for="dict_item in fbdict" 
                :key="dict_item.index"
                >
                  <td 
                  v-for="item in dict_item" 
                  :key="item.index"
                  >
                  <span id='tcont'>{{ item }}</span>
                  </td>
                </tr>
              </thead>
            </table>
          </div>
        </div>
      </div>
      <div class="text-center container">
        <span>Page <input
          v-model="curPage"
          style="width:3rem"
          > of {{ totalPage }} </span>
        <b-button
        @click='MetInfo()'
        >GO</b-button>
      </div>
    </div>
    <br/><br/><br/><br/><br/><br/><br/><br/><br/><br/><br/><br/><br/>
  </div>
</template>


<script>
import axios from 'axios'
// import Bus from '../api/bus.ts'
import LoadingVue from '@/components/LoadingVue.vue'


export default {
  name: 'DatabaseVue',
  components: { LoadingVue },
  data () {
    return {
      isLoading: false,
      metInfo: false,
      metGene: false,
      metSen: false,
      rowhover: false,
      curPage: 1,
      totalPage: '',
      watchingId: '',
      MetInfoList: [],
      MetGeneList: [],
      MetSensorList: [],
      fbdict: {},
      titles: [],
      homeKeyword: ''
      }
  },
  created () {
    this.isLoading = true,
    this.metInfo = false,
    this.metGene = false,
    this.metSen = true,
    this.getMetSensorData(),
    this.isLoading = false
  },
  methods: {
    // query data from backend
    getMetInfoData () {
      return axios({
        url: 'https://raw.githubusercontent.com/zhengrongbin/zhengrongbin.github.io/master/filles/met_ann.tsv',
        method: 'get',
      })
        .then(response => {
          this.MetInfoList = response.data
          var data = this.MetInfoList
          this.fbdict = this.readDoc(data)
        })
        .catch(error => {
          console.warn(error)
          this.showToast('Notice!', 'Sorry for loading failed :(', 'warning)')
        })
    },
    getMetGeneData () {
      return axios({
        url: 'https://raw.githubusercontent.com/zhengrongbin/zhengrongbin.github.io/master/filles/met_re.tsv',
        method: 'get',
      })
        .then(response => {
          this.MetGeneList = response.data
          var data = this.MetGeneList
          this.fbdict = this.readDoc(data)
        })
        .catch(error => {
          console.warn(error)
          this.showToast('Notice!', 'Sorry for loading failed :(', 'warning)')
        })
    },
    getMetSensorData () {
      return axios({
        url: 'https://raw.githubusercontent.com/zhengrongbin/zhengrongbin.github.io/master/filles/met_sen.tsv',
        method: 'get',
      })
        .then(response => {
          this.MetSensorList = response.data
          var data = this.MetSensorList
          this.fbdict = this.readDoc(data)
        })
        .catch(error => {
          console.warn(error)
          this.showToast('Notice!', 'Sorry for loading failed :(', 'warning)')
        })
    },
    showToast: function (title, content, color) {
      this.$bvToast.toast(content, {
        title: title,
        variant: color,
        solid: true
      })
      },
    readDoc (data) {
      var fbdict = new Array();
      // alert(data)
      var lines = data.split("\n");
      var totalLines = lines.length-1
      var titles = lines[0].split('\t')
      lines = lines.slice(1, totalLines)
      this.titles = titles
      var eachPage = 15;
      // total page
      this.totalPage = parseInt((totalLines - 1) / eachPage) + 1
      if ( this.curPage > this.totalPage) {
        this.curPage = this.totalPage
      }
      // change page
      var curNum = this.curPage 
      var befnum = eachPage * curNum - eachPage;
      var aftnum = eachPage * curNum;
      var new_lines = lines.slice(befnum, aftnum);

      for (var i = 0; i < new_lines.length; i++) {
          var tab = new_lines[i];
          var elems = tab.split('\t');
          var elems_dict = new Object();
          for (var j = 0; j < elems.length; j++) {
            elems_dict[titles[j]] = elems[j]
          }
          // var key = elems[0];
          fbdict.push(elems_dict);
      }
      return(fbdict)
    },
    MetInfo () {
      this.isLoading = true,
      this.metGene = false,
      this.metSen = false,
      this.metInfo = true
      this.getMetInfoData(),
      this.isLoading = false
    },
    MetGene () {
      this.isLoading = true,
      this.metInfo = false,
      this.metSen = false,
      this.metGene = true,
      this.getMetGeneData(),
      this.isLoading = false
    },
    MetSensor () {
      this.isLoading = true,
      this.metInfo = false,
      this.metGene = false,
      this.metSen = true,
      this.getMetSensorData(),
      this.isLoading = false
      // alert(this.MetSensorList)
      // this.fbdict = this.readDoc(this.MetSensorList)
    }
  }
}
</script>

